import DynamicJS from "@/js/core/dynamicjs";
export default {
  data() {
    return {
      without_px: [
        "--frame-input-label-alignment",
        "--base-font-color",
        "--frame-background-image",
        "--frame-background-color",
        "--frame-input-background-color",
        "--body-background-color",
        "--frame-inner-elements",
        "--frame-radious",
        "--formbuilder-header",
        "--frame-background-size",
        "--frame-background-position",
        "--frame-background-repeat",
        "--text-color",
        "--background-color",
        "--font-family",
        "--frame-label-display",
        " --body-background",
        "--formbuilder-input-border-color",
        "--formbuilder-btn-bg",
        "--section-bg-color",
        " --formbuilder-btn-color",
        "--form-focus-border-color",
        "--frame-label-style",
        "--form-no-labels",
        "--frame-banner-position",
        "--frame-heading",
        "--frame-description",
        "--frame-helptext",
        "--frame-label",
      ],
      children: "",
      children_category: "",
    };
  },
  methods: {
    async advance_theme_data(selector) {
      let theme = JSON.parse(selector.theme);
      await DynamicJS.downloadCSS("/css/forms/advance-theme.css", "advance_theme_builder");
      let style = document.getElementsByClassName("form-builder")[0]?.style;
      if (style) {
        // Set CSS properties to the CSS root properties
        // delete theme.color_schema;
        for (var key in theme) {
          style.setProperty(key, this.without_px.indexOf(key) === -1 ? `${theme[key]}px` : key === "--frame-background-image" ? `url(${theme[key]})` : theme[key]);
        }
      }
    },
  },
};
